.appWrapper {
    padding: 30px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    position: absolute;
    top: 270px;
    left: 340px;
    gap: 30px;
}

.settingsBlock {
    display: grid;

}

.informationPanelBlock {
    display: grid;
}

.settings {
    display: grid;
    padding: 10px;
    color: aqua;
    justify-content: space-around;
    align-content: space-around;
    width: 400px;
    height: 200px;
    border: 4px solid aqua;
    border-radius: 10px;
    gap: 10px;
}

.controlInformationPanel {
    display: grid;
    justify-content: center;
    align-items: center;
    border: 4px solid aqua;
    border-radius: 10px;
}

.customization {
    display: grid;
    justify-content: center;
    align-self: center;
    width: 300px;
    height: 100px;
    border: 3px solid aqua;
    border-radius: 10px;
}

.maxValue {
    display: grid;
    width: 280px;
    height: 25px;
}

.startValue {
    display: flex;
    width: 280px;
    height: 25px;
}

.enterCuztomization {
    display: flex;
    justify-self: center;
    border: 3px solid aqua;
    padding: 20px 125px;
    border-radius: 10px;
}

.info {
    display: grid;
    color: aqua;
    justify-content: center;
    border-radius: 10px;
    align-items: center;
    width: 340px;
    height: 130px;
    border: 3px solid aqua;
}

.errorInfo {
    display: grid;
    color: red;
    justify-content: center;
    border-radius: 10px;
    align-items: center;
    width: 340px;
    height: 130px;
    border: 3px solid red;
}

.control {
    display: flex;
    justify-content: center;
    gap: 20px;
    padding: 5px;
    border-radius: 10px;
    justify-items: center;
    border: 3px solid aqua;
}

.button {
    display: grid;
    justify-content: center;
    align-content: center;
    background-color: aqua;
    font-size: medium;
    font-weight: bold;
    width: 50px;
    height: 25px;
    border-radius: 10px;
    border: none;
}

.input {
    display: inline-block;
    height: 20px;
    text-align: center;
    grid-row-end: auto;
    border: 2px solid aqua;
    border-radius: 10px;
}

.inputError {
    display: inline-block;
    height: 20px;
    background-color: palevioletred;
    text-align: center;
    grid-row-end: auto;
    border: 2px solid darkred;
    border-radius: 10px;
}

.error {
    color: red;
    text-align: center;
    font-size: large;
    font-weight: bold;
}